<template>
  <div>
    <div class="container text-center mt-1 mb-1">
      <font-awesome-icon class="bg-pinky" :icon="['fas', 'grip']"/>
      <font-awesome-icon class="bg-pinky" :icon="['fas', 'grip-lines']"/>
    </div>
    <nav class="navbar navbar-expand-lg">
      <div class="container-fluid">
        <div class="collapse navbar-collapse" id="navbarSupportedContent">
          <MenuComponent v-on:view_all="goToPicsAll" style="margin-left: 40%" />
        </div>
      </div>
    </nav>

    <div id="pics" class="container text-center mt-5 mb-5">
      <main v-if="pics_hide">
        <div v-for="pic of pics" :key="pic.id">
          <img :hidden="pic.hide" :src="pic.url" />
        </div>
      </main>
      
      <main v-if="pics_uneven_hide">
        <div v-for="pic of pics_uneven" :key="pic.id">
          <img :hidden="pic.hide" :src="pic.url" />
        </div>
      </main>

      <main v-if="pics_even_hide">
        <div :class="pic.class_pic" v-for="pic of pics_even" :key="pic.id">
          <img :hidden="pic.hide" :src="pic.url" />
        </div>
      </main>
    </div>

    <div class="d-flex align-items-center flex-column">
      <a
        class="nav-link bg-pink active m-med text-center"
        aria-current="page"
        @click="viewAll(view_all,pics_event)"
        >{{text_view}}</a
      >
    </div>
  </div>
</template>

<script>
import MenuComponent from "../MenuComponet/index.vue";
const axios = require("axios").default;

export default {
  name: "indexMenu",
  data() {
    return {
      pics: [],
      pics_uneven: [],
      pics_even: [],
      pics_uneven_hide: false,
      pics_even_hide: false,
      text_view: "Show Me More",
      view_all:true,
      pics_hide:true,
      pics_event: "all"
    };
  },
  props: {
    view_all_c: []
  },
  components: {
    MenuComponent
  },
  created() {
    this.getPics();
  },
  mounted() {
    console.log(this.view_all_c);
  },
  watch: {
    // cada vez que la pregunta cambie, esta función será ejecutada
    view_all_c: function (view_all) {
      this.goToPicsAll(view_all);
    }
  },
  methods: {
    async getPics() {
      await axios
        .get("https://jsonplaceholder.typicode.com/albums/1/photos")
        .then((response) => {
          /* let options =["item-a","item-b","item-c"]; */
          this.pics = response.data;
          let length_pics = this.pics.length;
          let k = 0;
          this.pics.map(function(x) {
            if((length_pics/2)-1 >= k ){
              x.hide = false;
            }else{
              x.hide = true;
            }

            k++;

          });

          this.pics_even = response.data.filter((pic) => pic.id % 2 == 0);
          let length_pics_even = this.pics_even.length;
          let i = 0;
          this.pics_even.map(function(x) {
            if((length_pics_even/2)-1 >= i ){
              x.hide = false;
            }else{
              x.hide = true;
            }

            i++;

          });

          this.pics_uneven = response.data.filter((pic) => pic.id % 2 !== 0);
          let length_pics_uneven = this.pics_uneven.length;
          let d= 0;
          this.pics_uneven.map(function(x) {
            if((length_pics_uneven/2)-1 >= d ){
              x.hide = false;
            }else{
              x.hide = true;
            }

            d++;

          });
        })
        .catch((error) => {
          console.log("Error Trayendo Informacion " + error.msg);
        });
    },
    viewAll(all_view,type){

      if(all_view && type==='even'){
        this.pics_even.map(function(x) {
                x.hide = false;
            });
            this.text_view = "Show Me Less"
            this.view_all = false;
            this.pics_uneven_hide = false;
            this.pics_even_hide = true;
            this.pics_hide = false;

      }else if(all_view && type==='uneven'){
        this.pics_uneven.map(function(x) {
                x.hide = false;
            });
        this.text_view = "Show Me Less"
        this.view_all = false;
        this.pics_uneven_hide = true;
        this.pics_even_hide = false;
        this.pics_hide = false;

      }else if(all_view && type==='all'){

        console.log("AQUUU");
        this.pics.map(function(x) {
                x.hide = false;
            });
        this.text_view = "Show Me Less"
        this.view_all = false;
        this.pics_uneven_hide = false;
        this.pics_even_hide = false;
        this.pics_hide = true;

      }


      if(!all_view && type==='even'){
        let length_pics_even = this.pics_even.length;
          let i = 0;
          this.pics_even.map(function(x) {
            if((length_pics_even/2)-1 >= i ){
              x.hide = false;
            }else{
              x.hide = true;
            }

            i++;

          });
            this.text_view = "Show Me More"
            this.view_all = true;
            this.pics_uneven_hide = false;
            this.pics_even_hide = true;
            this.pics_hide = false;
      }else if(!all_view && type==='uneven'){
        let length_pics_uneven = this.pics_uneven.length;
          let i = 0;
          this.pics_uneven.map(function(x) {
            if((length_pics_uneven/2)-1 >= i ){
              x.hide = false;
            }else{
              x.hide = true;
            }

            i++;

          });
        this.text_view = "Show Me More"
        this.view_all = true;
        this.pics_uneven_hide = true;
        this.pics_even_hide = false;
        this.pics_hide = false;
      }else if(!all_view && type==='all'){
        let length_pics = this.pics.length;
          let i = 0;
          this.pics.map(function(x) {
            if((length_pics/2)-1 >= i ){
              x.hide = false;
            }else{
              x.hide = true;
            }

            i++;

          });
        this.text_view = "Show Me More"
        this.view_all = true;
        this.pics_uneven_hide = false;
        this.pics_even_hide = false;
        this.pics_hide = true;
      }

    },
    goToPicsAll(view_all){

      if(view_all[0].view_all){
        this.viewAll(false,"all")
        this.pics_event = "all";
      }

      if(view_all[0].view_even){
        this.viewAll(false,"even")
        this.pics_event = "even";
      }

      if(view_all[0].view_uneven){
        this.viewAll(false,"uneven")
        this.pics_event = "uneven";
      }

      this.scrollInto('pics');
      
    },
    scrollInto(elementId) {
      const section = document.querySelector(`#${elementId}`);
      section.scrollIntoView({
        behavior: 'smooth'
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
main {
  max-width: 80%;
  padding: 1rem;
  margin: 0 auto;
  margin-left: 20%;
  display: grid;
grid-template-columns: repeat(auto-fit, 250px);
  grid-template-rows: 300px 300px;
  grid-gap: 1rem;
  
}

img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.item-a {
  grid-row: 1 / 3;
}

.item-b {
  grid-column: 3 / 5;
}

.item-c {
  grid-row: 2 / 4;
}

.bg-pink.active {
  background-color: #ff5672;
  color: white !important;
}

.m-med {
  margin-top: 3%;
  margin-bottom: 2%;
  padding: 0.5%;
  width: 10%;
  font-weight: bold;
}

.bg-pinky{
  color:#ff5672;
  margin:0.5em;
}
</style>
