<template>
    <div>
        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
            <li class="nav-item">
              <a class="nav-link" :class="class_active_all" aria-current="page" @click="_goToAll"
                >All</a
              >
            </li>
            <li class="nav-item">
              <a class="nav-link" :class="class_active_brand" @click="_goToBrand">Branding</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" :class="class_active_web" @click="_goToWeb">Web</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" :class="class_active_photo" href="#"  @click="_goToPhoto">Photography</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" :class="class_active_app" href="#" @click="_goToApp">App</a>
            </li>
          </ul>
    </div>
  </template>
  
  <script>
  export default {
    name: "indexMenu",
    data() {
    return {
      class_active_all: "bg-pink active",
      class_active_brand: "",
      class_active_web: "",
      class_active_photo: "",
      class_active_app: ""
    };
  },
    props: ['goToPics'],
    methods:{
      _goToAll(){
        let views = [{"view_all":true,"view_even":false,"view_uneven":false}]
        this.$emit('view_all', views);
        this.class_active_all = "bg-pink active";
        this.class_active_brand = "";
        this.class_active_web = "";
        this.class_active_photo = "";
        this.class_active_app = "";
      },
      _goToBrand(){
        let views = [{"view_all":false,"view_even":true,"view_uneven":false}]
        this.$emit('view_all', views);
        this.class_active_all = "";
        this.class_active_brand = "bg-pink active";
        this.class_active_web = "";
        this.class_active_photo = "";
        this.class_active_app = "";
      },
      _goToWeb(){
        let views = [{"view_all":false,"view_even":false,"view_uneven":true}]
        this.$emit('view_all', views);
        this.$emit('view_all', views);
        this.class_active_all = "";
        this.class_active_brand = "";
        this.class_active_web = "bg-pink active";
        this.class_active_photo = "";
        this.class_active_app = "";
      },
      _goToPhoto(){
        this.class_active_all = "";
        this.class_active_brand = "";
        this.class_active_web = "";
        this.class_active_photo = "bg-pink active";
        this.class_active_app = "";
      },
      _goToApp(){
        this.class_active_all = "";
        this.class_active_brand = "";
        this.class_active_web = "";
        this.class_active_photo = "";
        this.class_active_app = "bg-pink active";
      },
    }
  };
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  .bg-pink.active {
    background-color: #ff5672;
    color: white !important;
    cursor:pointer;
  }

  .nav-item {
    cursor:pointer;
  }
  </style>
  